import React from 'react';
import 'animate.css'
import './components/mainrow.css'
import './App.css';
import vid from './media/psmenubackground.mp4'
import SelectedBox from './components/selectedbox';
import SmallBox from './components/smallbox';
import MainRow from './components/mainrow';
import TopBar from './components/topbar';

class App extends React.Component{
  state={
    time:null
  }
  getTime=()=>{
   let d = new Date();
    let hour = d.getHours()
    let m= "AM"
  if(hour===0){
    hour=12
    m="AM"
  }else{
    hour=hour>11?(hour-12):hour
    m="PM"
  }
 
    let time = `${hour}:${d.getMinutes()<10?`0${d.getMinutes()}`:d.getMinutes()} ${m}`
  this.setState({
    time: time
  })
  }
  
  componentDidMount(){
    this.getTime()
    setInterval(() => {
      this.getTime()
    }, 1000);
  }

  render(){
    return (
      <div className="App">
        <video autoplay="true" muted="true" loop="true" id="video">
  
          <source src={vid} type="video/mp4"/>
        </video>
  
  
        <TopBar 
        notifications={5} 
        clock={this.state.time}
        message="New message!!!"
        username="Username"
        img="data:image/jpeg;base64,"/>
        <MainRow/>
      </div>
    );
  }
  
}

export default App;
